







































import Vue from 'vue'
import { mapState } from 'vuex'
import SidebarTrackingSection from './Tracking.vue'
import SidebarFooter from './Footer.vue'

export default Vue.extend({
  components: { SidebarTrackingSection, SidebarFooter },
  data() {
    return { activeCampaign: null as Types.Campaign | null }
  },
  computed: {
    ...mapState(['campaigns']),

    campaign: {
      get(): Types.Campaign | null {
        if (this.activeCampaign) {
          return this.activeCampaign
        }

        return this.hasCampaigns ? this.campaigns[this.campaigns.length - 1] : null
      },
      set(campaign: Types.Campaign): void {
        this.activeCampaign = campaign
      }
    },

    hasCampaigns(): boolean {
      return this.campaigns.length > 0
    }
  },

  methods: {
    openModal(): void {
      window._backend
        .withSuccessHandler(function() {
          window._frontend.close()
        })
        .sheetUIShowModal()
    },

    sentDate(campaign: Types.Campaign): string {
      if (!campaign) {
        return ''
      }

      const dateObj = new Date(campaign.createdAt || '')

      if (typeof dateObj.toLocaleString !== 'function') {
        return dateObj.toString()
      }

      const date = dateObj.toLocaleString(undefined, { year: 'numeric', month: 'numeric', day: 'numeric' })
      const time = dateObj.toLocaleString(undefined, { hour: '2-digit', minute: '2-digit' })

      return date + ' at ' + time
    }
  }
})
